import Toast from '../../wxcomponents/vant/dist/toast/toast';
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
import LbPicker from '../base/lb-picker'
import UniIcons from '../base/uni-icons/uni-icons'
var app = getApp().globalData
export default {
	components: {
	    LbPicker,
		UniIcons
	},
	data() {
		return {
			myProps: {
			  label: 'name',
			  value: 'sn',
			  children: 'child'
			},
			description:"",
			showtext:true,
			shop_url:'',
			address:'',
			city:'',
			citySn:'',
			province:'',
			provinceSn:'',
			contact:'',
			job:'',
			status:'0',
			id:'',
			finish:false,
			fileList:[], //图片本地地址列表
			imgUrl2:[],
			list:[], // 城市列表
			logo:'',
			finish1 : false,
			short_name:''
		}
	},
	onLoad(e) {
		this.status = e.status
		this.id = e.id
		// 0是重新编辑
		if(e.status=='0'){
			// 获取默认值
			this.getData()
		}else{
			this.getCity()
		}
		
		
	},
	watch: {
	    finish () {
	      if (this.finish) {
	        uni.hideLoading();
	        this.$api.company_authentication_second({
	          token: uni.getStorageSync("token"),
	          user_id: uni.getStorageSync("userId"),
			  logo_image:this.imgUrl2,
			  contact:this.contact,
	          job: this.job,
	          shop_url: this.shop_url,
	          description: this.description,
	          province: this.provinceSn,
	          city: this.citySn,
			  id:this.id,
			  short_name:this.short_name
	        }).then(res => {
				// console.log(res)
			  Toast(res.data.msg)
	          // this.$toast(res.msg)
	          if (res.data.code === '200') {
				  uni.setStorageSync('company_id',res.data.data.company_id)
				  setTimeout(()=>{
					  uni.redirectTo({
					  	 url:'../../pagesTwo/MyBusiness/MyBusiness?id='+uni.getStorageSync("company_id")
					  })
				  }, 1000);
				 
	          }
	        })
	      }
	    },
		finish1 () {
		  if (this.finish1) {
		    uni.hideLoading();
		    this.$api.update_company({
		      token: uni.getStorageSync("token"),
		      user_id: uni.getStorageSync("userId"),
			  logo_image:this.imgUrl2,
			  contact:this.contact,
		      job: this.job,
		      shop_url: this.shop_url,
		      description: this.description,
		      province: this.provinceSn,
		      city: this.citySn,
			  id:this.id,
			  short_name:this.short_name
		    }).then(res => {
				// console.log(res)
			  Toast(res.data.msg)
		      // this.$toast(res.msg)
		      if (res.data.code === '200') {
				  setTimeout(()=>{
					  uni.navigateBack({
					  	delta:1
					  })
				  }, 1000);
				 
		      }
		    })
		  }
		}
	},
	methods: {
		getData(){
			this.$api.update_company_default({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
			}).then(res=>{
				// console.log(res)
				this.fileList.push({url:res.data.data.logo})
				this.job = res.data.data.job
				this.contact = res.data.data.contact
				this.shop_url = res.data.data.shop_url
				this.id = res.data.data.id
				this.description = res.data.data.description
				this.provinceSn = res.data.data.province
				this.citySn = res.data.data.city
				this.logo = res.data.data.logo
				this.short_name = res.data.data.short_name
				// console.log(this.short_name)
				this.$nextTick(()=>{
					this.getCity()
				})
			})
		},
		// 重新编辑的发布
		send1 () {
			  var that = this	      
		      if (this.job && this.contact && this.address && this.description && this.short_name &&this.fileList.length > 0 ) {
				  
				  if(this.fileList[0].url.indexOf('aliyuncs.com')==-1){
					  Dialog.confirm({
					    title: '发布提示',
					    message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担，确认发布？'
					  }).then(() => {
					    uni.showLoading({
					        title: '加载中'
					    });
					    uni.uploadFile({
					  		  url: app.aliUrl,
					  		  filePath: that.fileList[0].url,
					  		  name: 'file',
					  		  formData: {
					  			  key:"wx/"+"${filename}",
					  			  signature: app.signature,
					  			  policy:app.policy,
					  			  OSSAccessKeyId:app.OSSAccessKeyId,
					  			  success_action_status:'201'
					  		  }
					   }).then(res=>{
					  	 // console.log(res)
					  	 that.imgUrl2.push({
					  		 hash:'',
					  		 url_image:res[1].data.split('<Location>')[1].split("</Location>")[0],
					  		 size:''
					  	 })
					  	 
					  	 if(that.imgUrl2.length>0){
					  		 that.imgUrl2 = JSON.stringify(that.imgUrl2).split('[')[1].split(']')[0]
					  		 that.finish1 = true
					  	 }
					   })
					    
					  }).catch(() => {
					  			
					  })
				  }else{
					  // 这个是用的阿里返回的图片,证明没改
					  Dialog.confirm({
					    title: '发布提示',
					    message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担，确认发布？'
					  }).then(() => {
						  
						  uni.showLoading({
						      title: '加载中'
						  });
						  that.imgUrl2.push({
								 hash:'',
								 url_image:that.logo,
								 size:''
						  })
						  that.imgUrl2 = JSON.stringify(that.imgUrl2).split('[')[1].split(']')[0]
						  that.finish1 = true
						  
					  }).catch(() => {
					  			
					  })
					  
				  }
				  
				  
		
		      } else {
				  if (this.fileList.length <= 0) {
				    Toast('请上传企业执照')
				    return
				  }
				  
		          if (!this.job) {
					  Toast('我的职位没有填写')
					  return
				  }
				  if (!this.contact) {
					  Toast('联系方式没有填写')
					  return
				  }
				  if (!this.address) {
					  Toast('请选择地址')
					  return
				  }
		          if (!this.description) {
					  Toast('请填企业简介')
					  return
		          }
				  if (!this.short_name) {
					  Toast('请填企业简称')
					  return
				  }
				
		      }
		},
		// 完善的发布
		send () {
			  var that = this	      
		      if (this.job && this.contact && this.address && this.description && this.short_name && this.fileList.length > 0 ) {
		        Dialog.confirm({
		          title: '发布提示',
		          message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担，确认发布？'
		        }).then(() => {
		          uni.showLoading({
		              title: '加载中'
		          });
				  uni.uploadFile({
						  url: app.aliUrl,
						  filePath: that.fileList[0].url,
						  name: 'file',
						  formData: {
							  key:"wx/"+"${filename}",
							  signature: app.signature,
							  policy:app.policy,
							  OSSAccessKeyId:app.OSSAccessKeyId,
							  success_action_status:'201'
						  }
				 }).then(res=>{
					 
					 that.imgUrl2.push({
						 hash:'',
						 url_image:res[1].data.split('<Location>')[1].split("</Location>")[0],
						 size:''
					 })
					 
					 if(that.imgUrl2.length>0){
						 that.imgUrl2 = JSON.stringify(that.imgUrl2).split('[')[1].split(']')[0]
						 that.finish = true
					 }
				 })
				  
		        }).catch(() => {
		
		        })
		      } else {
				  if (this.fileList.length <= 0) {
				    Toast('请上传企业执照')
				    return
				  }
				  
		          if (!this.job) {
					  Toast('我的职位没有填写')
					  return
				  }
				  if (!this.contact) {
					  Toast('联系方式没有填写')
					  return
				  }
				  if (!this.address) {
					  Toast('请选择地址')
					  return
				  }
		          if (!this.description) {
					  Toast('请填企业简介')
					  return
		          }
				  if (!this.short_name) {
					  Toast('请填企业简称')
					  return
				  }
				
		      }
		},
		
		// 获取城市
		getCity(){
			this.$api.E_areas_list({
				
			}).then(res=>{
				// console.log(res)
				this.list = res.data.data
				if(this.status=='0'){
					for(var i = 0 ; i<this.list.length;i++){
						if(this.provinceSn == this.list[i].sn){
							// console.log(this.list[i])
							this.address = this.list[i].name
							for(var j = 0;j<this.list[i].child.length;j++){
								if(this.citySn == this.list[i].child[j].sn){
									this.address = this.address + ' ' +this.list[i].child[j].name
								}
							}
						}
					}
				}
				
			})
		},
		chooseCity(){
			this.$refs.pic1.show()
		},
		hidep(){
			this.showtext = true
		},
		showp(){
			this.showtext = false
		},
		ok(e){
			// console.log(e)
			this.address = e.item[0].name+ ' ' + e.item[1].name
			this.provinceSn = e.value[0]
			this.citySn = e.value[1]
		},
		// 删除图片
		del(e){
			this.fileList = this.fileList.filter((item,index)=> index!=e.detail.index)
		},
		// 图片上传后的预览
		afterRead(event) {
		      const { file } = event.detail;
		      this.fileList.push({url:file[0].path})
		},
	}
}
